import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid, LinearProgress, List, ListItem, ListItemSecondaryAction, ListItemText, Modal, Paper, Radio, TextField, Typography, withStyles } from "@material-ui/core";
import * as React from "react";
import { useMemo } from "react";
import ajax, { useFetcher } from "../ajax";
import { useUserInfo } from "../save";
import { primaryColor, useModalTheme } from "../style";
import Defer from "./Defer";
import AddIcon from '@material-ui/icons/CloudUpload';
import DelIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import SwitchIcon from '@material-ui/icons/UnfoldMore';
import PlaceIcon from '@material-ui/icons/Place';
import { navigate } from "gatsby";
import { expandImg, winalert } from "../util";


/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    gallery: {
        '& .MuiGrid-item.selected': {
            background: primaryColor,
        },
        '& img': {
            width: 'calc(100% - 10px)',
            height: 100,
            objectFit: 'cover',
            margin: 5,
        },
    },
    thumb: {
        '& img': {
            width: 50,
            objectFit: 'contain',
            margin: 5,
        },
    },
})

const MediaPrompt = withStyles(styles)(({ open, value, onChange, onClose, classes, multiple }) => {
    const user = useUserInfo();
    const [ref, setRef] = React.useState(0);
    const list = useFetcher(user && '/uploads/#ref=' + ref);
    /** @type {React.MutableRefObject<HTMLInputElement>} */
    const inputRef = React.useRef();
    const [uploading, setUploading] = React.useState(0);
    const openUpload = () => {
        inputRef.current.click();
    }
    const processUpload = async () => {
        var files = inputRef.current.files;
        if (files.length == 0) return;
        let i = 0;
        setUploading(0.01);
        try {
            let formData = new FormData(inputRef.current.form);
            await ajax.post('/uploads/', formData, {
                onUploadProgress: (p) => {
                    setUploading(((p.loaded / p.total) + i) / files.length);
                }
            });
            i++;
        } catch (error) {
            await winalert("Terjadi kesalahan saat mengupload, coba cek koneksi anda");
        }
        setUploading(0);
        setRef(ref + 1);
    }
    return (
        <Dialog className={classes.gallery} open={open} onClose={onClose} fullWidth>
            <form>
                <input type="file" name="files" hidden ref={inputRef} multiple onChange={processUpload} />
            </form>
            <DialogTitle>Pilih Media</DialogTitle>
            <DialogContent>
                <Defer data={list}>
                    {() =>
                        <Grid container>
                            {
                                multiple && list.data.map(x => <Grid className={value.includes(x.id) ? 'selected' : ''} data-d={value.includes(x.id) ? 'selected' : 'b'} xs={4} key={x.id} item onClick={() => onChange(() => {
                                    if (!Array.isArray(value))
                                        value = [];
                                    if (value.includes(x.id)) {
                                        value.splice(value.findIndex((y) => y == x.id), 1);
                                    } else {
                                        value.push(x.id);
                                    }
                                    console.log(value);
                                    return [...value];
                                })}>
                                    <img src={expandImg(x.id, "thumbnail")} alt="" />
                                </Grid>)
                            }{
                                !multiple && list.data.map(x => <Grid xs={4} key={x.id} className={value === x.id ? 'selected' : ''} item onClick={() => onChange(() => {
                                    return (value === x.id ? null : x.id);
                                })}>
                                    <img src={expandImg(x.id, "thumbnail")} alt="" />
                                </Grid>)
                            }
                            {
                                list.data.length === 0 && <Grid xs={12} item>
                                    <Typography align="center" onClick={openUpload}>
                                        Media kosong. Silahkan upload dulu.
                                    </Typography>
                                </Grid>
                            }
                        </Grid>}
                </Defer>
            </DialogContent>
            <DialogActions>
                <Button startIcon={<AddIcon />} onClick={openUpload} variant="contained">
                    Upload
                </Button>
                <Box flexGrow={1} >
                    {uploading !== 0 && <LinearProgress value={uploading * 100} variant="determinate" />}
                </Box>
                <Button onClick={onClose} variant="contained" color="primary">Tutup</Button>
            </DialogActions>
        </Dialog>
    );
});

const MediaInput = withStyles(styles)(({ value, onChange, classes, multiple = false }) => {
    const [open, setOpen] = React.useState(false);
    return <div className={classes.thumb}>
        {multiple ? <Button endIcon={<SwitchIcon />} onClick={() => setOpen(true)} variant="contained">
            {value.map(x => x && <img key={x} src={expandImg(x, "thumbnail")} alt="" />)}
            {value.length === 0 && <div style={{ fontStyle: 'italic' }}>Tambahkan media</div>}
        </Button> : <Button endIcon={<SwitchIcon />} onClick={() => setOpen(true)} variant="contained">
            {value ? <img src={expandImg(value, "thumbnail")}  alt="" /> : 'Pilih Media'}
        </Button>}
        <MediaPrompt open={open} multiple={multiple} value={value} onChange={onChange} onClose={() => setOpen(false)} />
    </div>
})

export default MediaInput
